import {
  Box,
  Divider,
  Grid,
  LinearProgress,
  Typography,
} from "@material-ui/core";
import React from "react";
import styles from "./VehicleHud.module.css";
import {
  CircularProgressbarWithChildren,
  buildStyles,
} from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

import { Icon, Stack } from "@mdi/react";
import {
  mdiEngineOutline,
  mdiSeatbelt,
  mdiCarLightDimmed,
  mdiCarLightHigh,
  mdiCarKey,
} from "@mdi/js";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowLeft,
  faArrowRight,
  faGasPump,
} from "@fortawesome/free-solid-svg-icons";

const VehicleHud = () => {
  const rpm: number = 95;
  return (
    <Box className={styles.mainBox}>
      <Box className={styles.speedoMeter}>
        <CircularProgressbarWithChildren
          value={rpm}
          circleRatio={0.75}
          styles={buildStyles({
            rotation: 1 / 2 + 1 / 8,
            strokeLinecap: "butt",
            trailColor: "#fff",
            pathColor: rpm < 90 ? "#82b1ff" : "#f44336",
          })}
        >
          <div className={styles.divInside}>
            <Grid item style={{ paddingTop: "2rem" }}>
              <Typography
                variant="h2"
                style={{ fontWeight: 800, fontSize: "4vw" }}
              >
                100
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="h5" style={{ marginTop: "-1rem" }}>
                / <span className={styles.kbd}>N</span>
              </Typography>
            </Grid>
            <Divider style={{ borderColor: "rgba(255, 255, 255, 1)" }} />
            <Grid item style={{ paddingTop: "0.2rem" }}>
              1000 km.
            </Grid>
            <Grid item style={{ paddingTop: "1rem" }}>
              <Icon
                path={mdiEngineOutline}
                size={1}
                className={styles.statusIcon}
              />
              <Icon
                path={mdiCarLightDimmed}
                size={1}
                className={styles.statusIcon}
              />
              <Icon
                path={mdiCarLightHigh}
                size={1}
                className={styles.statusIcon}
              />
              <Icon path={mdiSeatbelt} size={1} className={styles.statusIcon} />
              <Icon path={mdiCarKey} size={1} className={styles.statusIcon} />
            </Grid>
            <Grid item style={{ paddingTop: "0.5rem" }}>
              <FontAwesomeIcon
                icon={faArrowLeft}
                pull="left"
                className={styles.statusIcon}
              />
              <FontAwesomeIcon
                icon={faArrowRight}
                pull="right"
                className={styles.statusIcon}
              />
              <Typography>
                <FontAwesomeIcon
                  icon={faGasPump}
                  style={{ paddingRight: "0.1vw" }}
                />
                100 | 100
              </Typography>
            </Grid>
          </div>
        </CircularProgressbarWithChildren>
      </Box>
    </Box>
  );
};

export default VehicleHud;
