import React from "react";
import { HashRouter as Switch, Route } from "react-router-dom";

import "./App.css";

import Login from "./components/Login/Login";
import CharacterList from "./components/Login/CharacterList";
import SpawnMenu from "./components/Login/SpawnMenu";
import Hud from "./components/Hud/Hud";

import { Logo } from "./components/Hud/Hud";

import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import { Link } from "react-router-dom";

const App: React.FC<{}> = () => {
  return (
    <div className="App">
      <Switch>
        <MenuShow />
        <Route path="/login">
          <Login />
        </Route>
        <Route path="/character">
          <CharacterList />
        </Route>
        <Route path="/spawnmenu">
          <SpawnMenu />
        </Route>
        <Route path="/main">
          <Logo />
          <Hud />
        </Route>
      </Switch>
    </div>
  );
};

const MenuShow = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event: any) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <div>
      <Button
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}
      >
        Open Menu
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={handleClose} component={Link} to="/login">
          Login
        </MenuItem>
        <MenuItem onClick={handleClose} component={Link} to="/character">
          Character
        </MenuItem>
        <MenuItem onClick={handleClose} component={Link} to="/spawnmenu">
          Spawn
        </MenuItem>
        <MenuItem onClick={handleClose} component={Link} to="/main">
          Hud
        </MenuItem>
      </Menu>
    </div>
  );
};

export default App;
