import React, { useState } from "react";
import { useFormik } from "formik";
import * as Yup from "yup";
import styles from "./Login.module.css";

import {
  Button,
  Grid,
  TextField,
  Paper,
  Divider,
  Typography,
  InputAdornment,
  CardMedia,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Checkbox,
  Link,
} from "@material-ui/core";

import Card from "../Global/Card/Card.js";
import CardBody from "../Global/Card/CardBody.js";
import CardHeader from "../Global/Card/CardHeader";
import ReactLogo from "../../assets/logo512.png";

// Icons
import {
  PersonAddSharp,
  ExitToAppSharp,
  ArrowBackSharp,
  ArrowForwardSharp,
  AccountCircleSharp,
  LockOpenSharp,
  MailOutlineSharp,
} from "@material-ui/icons";

declare var mp: any;

const ShowPage: React.FC<{}> = () => {
  return (
    <div className={styles.div}>
      <Paper className={styles.paper}>
        <Grid
          container
          justifyContent="center"
          alignItems="center"
          style={{ minHeight: "60vh" }}
        >
          <Grid item xs className={styles.grid}>
            <Card>
              <CardMedia component="img" image={ReactLogo} />
            </Card>
          </Grid>
          <Divider orientation="vertical" flexItem />
          <Grid item xs className={styles.grid}>
            <Login />
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
};

const Login: React.FC<{}> = () => {
  const [toggled, setToggled] = useState(true);
  const [errorMessage, seterrorMessage] = useState("");
  const [dialogOpen, setDialogOpen] = useState(false);
  const loginCheck = () => {
    seterrorMessage("SUKA");
    return 1;
  };
  (window as any).loginCheck = loginCheck;
  const login = useFormik({
    initialValues: {
      name: "",
      password: "",
    },
    validationSchema: Yup.object({
      name: Yup.string().required("    ."),
      password: Yup.string().required("    ."),
    }),
    onSubmit: (values) => {
      //alert(JSON.stringify(values));
      mp.trigger(
        "Authorization.LoginDataToServer",
        values.name,
        values.password,
        0
      );
    },
  });

  const register = useFormik({
    initialValues: {
      name: "",
      email: "",
      password_1: "",
      password_2: "",
      checkbox: false,
    },
    validationSchema: Yup.object({
      name: Yup.string()
        .required("    .")
        .min(4, " -  - 4.")
        .max(16, " -  - 16.")
        .matches(
          /^[a-zA-Z]+$/,
          "       ."
        ),
      email: Yup.string()
        .required("    .")
        .email("Email  - ."),
      password_1: Yup.string()
        .required("    .")
        .min(6, " -  - 6.")
        .max(16, " -  - 16."),
      password_2: Yup.string()
        .required("    .")
        .oneOf([Yup.ref("password_1"), ""], "  ."),
    }),
    onSubmit: (values) => {
      alert(JSON.stringify(values));
    },
  });
  if (toggled) {
    return (
      <form onSubmit={login.handleSubmit}>
        <Grid
          container
          direction="column"
          alignItems="center"
          justifyContent="center"
        >
          <Card>
            <CardHeader color="primary">
              <Typography variant="h5" color="initial">
                
              </Typography>
            </CardHeader>
            <CardBody>
              <TextField
                name="name"
                label=""
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={login.handleChange}
                value={login.values.name}
                error={login.touched.name && Boolean(login.errors.name)}
                helperText={login.touched.name && login.errors.name}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <AccountCircleSharp />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                name="password"
                label=""
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "10px",
                }}
                onChange={login.handleChange}
                value={login.values.password}
                error={login.touched.password && Boolean(login.errors.password)}
                helperText={login.touched.password && login.errors.password}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <LockOpenSharp />
                    </InputAdornment>
                  ),
                }}
              />
              {errorMessage && (
                <Typography
                  id="errorMessage"
                  style={{
                    textAlign: "left",
                  }}
                >
                  {errorMessage}
                </Typography>
              )}
              <Grid
                container
                direction="row"
                justifyContent="flex-end"
                spacing={1}
              >
                <Grid item>
                  <Button
                    size="medium"
                    variant="contained"
                    color="primary"
                    type="submit"
                    disabled={!login.dirty}
                  >
                    <ExitToAppSharp />
                    
                  </Button>
                </Grid>
                <Grid item>
                  <Button
                    size="medium"
                    variant="contained"
                    color="secondary"
                    type="reset"
                    onClick={() => setToggled((currToggled) => !currToggled)}
                  >
                    <PersonAddSharp />
                    
                  </Button>
                </Grid>
              </Grid>
            </CardBody>
          </Card>
        </Grid>
      </form>
    );
  } else {
    return (
      <form onSubmit={register.handleSubmit}>
        <Grid
          container
          direction="column"
          alignItems="center"
          justifyContent="center"
        >
          <Card className={styles.card}>
            <CardHeader color="primary">
              <Typography variant="h5" color="initial">
                
              </Typography>
            </CardHeader>
            <CardBody>
              <TextField
                name="name"
                label=""
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={register.handleChange}
                value={register.values.name}
                error={register.touched.name && Boolean(register.errors.name)}
                helperText={register.touched.name && register.errors.name}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <AccountCircleSharp />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                name="email"
                label="Email"
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={register.handleChange}
                value={register.values.email}
                error={register.touched.email && Boolean(register.errors.email)}
                helperText={register.touched.email && register.errors.email}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <MailOutlineSharp />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                name="password_1"
                label=""
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={register.handleChange}
                value={register.values.password_1}
                error={
                  register.touched.password_1 &&
                  Boolean(register.errors.password_1)
                }
                helperText={
                  register.touched.password_1 && register.errors.password_1
                }
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <LockOpenSharp />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                name="password_2"
                label=" "
                variant="outlined"
                fullWidth
                style={{
                  marginBottom: "2em",
                }}
                onChange={register.handleChange}
                value={register.values.password_2}
                error={
                  register.touched.password_2 &&
                  Boolean(register.errors.password_2)
                }
                helperText={
                  register.touched.password_2 && register.errors.password_2
                }
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <LockOpenSharp />
                    </InputAdornment>
                  ),
                }}
              />
              <Typography>
                <Checkbox
                  name="checkbox"
                  color="primary"
                  onChange={register.handleChange}
                  value={register.values.checkbox}
                />
                <Link color="inherit" onClick={() => setDialogOpen(true)}>
                        
                </Link>
              </Typography>
              <Dialog open={dialogOpen}>
                <DialogTitle> </DialogTitle>
                <DialogContent>asd</DialogContent>
                <DialogActions>
                  <Button
                    variant="outlined"
                    color="inherit"
                    onClick={() => setDialogOpen(false)}
                  >
                    
                  </Button>
                </DialogActions>
              </Dialog>
              <Grid
                container
                direction="row"
                justifyContent="flex-end"
                spacing={1}
              >
                <Grid item>
                  <Button
                    size="medium"
                    variant="contained"
                    color="primary"
                    type="submit"
                    disabled={!register.dirty || !register.values.checkbox}
                  >
                    <ArrowForwardSharp />
                    
                  </Button>
                </Grid>
                <Grid item>
                  <Button
                    size="medium"
                    variant="contained"
                    color="secondary"
                    onClick={() => setToggled((currToggled) => !currToggled)}
                  >
                    <ArrowBackSharp />
                    
                  </Button>
                </Grid>
              </Grid>
            </CardBody>
          </Card>
        </Grid>
      </form>
    );
  }
};

export default ShowPage;
