import { Box, Divider, Grid, Icon, Typography } from "@material-ui/core";
import React from "react";
import styles from "./Hud.module.css";
import {
  CircularProgressbarWithChildren,
  buildStyles,
} from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import VehicleHud from "./VehicleHud";

import {
  FastfoodOutlined,
  LocalDrinkOutlined,
  SentimentVerySatisfiedOutlined,
  MoodOutlined,
  MoodBadOutlined,
  PinDropOutlined,
  LocalAtmOutlined,
  AccountBalanceOutlined,
  ScheduleOutlined,
  DateRangeOutlined,
  MicNoneOutlined,
  MicOffOutlined,
} from "@material-ui/icons";

const Hud = () => {
  let formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  });
  const [time, setTime] = React.useState(new Date());
  React.useEffect(() => {
    const timer = setInterval(() => {
      setTime(new Date());
    }, 1000);
    return () => {
      clearInterval(timer);
    };
  });
  const clock = time.toLocaleTimeString("ru-RU", {
    hour: "numeric",
    minute: "numeric",
  });
  const date = time.toLocaleDateString("ru-RU");
  const day = new Intl.DateTimeFormat("ru-RU", { weekday: "long" }).format(
    time
  );

  return (
    <div className={styles.div}>
      <VehicleHud />
      <Box className={styles.leftBottom}>
        <LeftBottomBar value={15} type="food" />
        <LeftBottomBar value={50} type="drink" />
        <LeftBottomBar value={100} type="mood" />
      </Box>
      <Box className={styles.leftBottomMain}>
        <AddItemToBar type="money" body={formatter.format(1000)} />
        <AddItemToBar type="bank" body={formatter.format(100000)} />
        <Grid container alignItems="center">
          <Grid item>
            <Typography
              style={{
                fontWeight: "bold",
                fontSize: "1.2rem",
                textAlign: "left",
              }}
            >
              {clock}
            </Typography>
            <Typography
              style={{
                fontWeight: "bold",
                fontSize: "0.8rem",
                display: "inline-block",
              }}
            >
              {date}
            </Typography>
          </Grid>
          <Grid item>
            <Typography
              style={{
                fontWeight: "bold",
                fontSize: "1.0rem",
                display: "inline-block",
                marginLeft: "5px",
                borderLeft: "1px solid white",
                paddingLeft: "5px",
              }}
            >
              {day.charAt(0).toUpperCase() + day.slice(1)}
            </Typography>
          </Grid>
        </Grid>
      </Box>
      <Box className={styles.leftBottomNav}>
        <Grid container>
          <Grid item>
            <PinDropOutlined fontSize="large" style={{ color: "#82b1ff" }} />
          </Grid>
          <Grid item>
            <Typography style={{ fontWeight: "bold", paddingRight: "5px" }}>
              Paleto Bay
            </Typography>
            <Typography style={{ fontSize: "0.5rem" }}>ASD</Typography>
          </Grid>
          <Divider orientation="vertical" flexItem />
          <MicOffOutlined fontSize="large" />
        </Grid>
      </Box>
    </div>
  );
};

export const Logo: React.FunctionComponent = () => {
  return (
    <div className={styles.logo}>
      <Box>
        <Typography>Flex-RP</Typography>
        <Typography>ID: 1000 [#100000]</Typography>
      </Box>
    </div>
  );
};

const LeftBottomBar = (props: any) => {
  const percentage: number = props.value;
  let IconImage, iconColor;
  switch (props.type) {
    case "food":
      IconImage = FastfoodOutlined;
      break;
    case "drink":
      IconImage = LocalDrinkOutlined;
      break;
    case "mood": {
      switch (true) {
        case percentage >= 75:
          IconImage = SentimentVerySatisfiedOutlined;
          break;
        case percentage < 75 && percentage > 25:
          IconImage = MoodOutlined;
          break;
        case percentage <= 25:
          IconImage = MoodBadOutlined;
          break;
        default:
          IconImage = SentimentVerySatisfiedOutlined;
      }
      break;
    }
    default:
      IconImage = LocalDrinkOutlined;
  }
  switch (true) {
    case props.value >= 75:
      iconColor = "#82b1ff";
      break;
    case props.value < 75 && props.value > 25:
      iconColor = "#ff9800";
      break;
    case props.value <= 25:
      iconColor = "#f44336";
      break;
    default:
      iconColor = "#82b1ff";
  }

  return (
    <div className={styles.circularProgress}>
      <CircularProgressbarWithChildren
        value={percentage}
        background
        styles={buildStyles({
          strokeLinecap: "butt",
          pathTransitionDuration: 0.5,
          pathColor: "#fff",
          trailColor: "transparent",
          backgroundColor: "rgba(0, 0, 0, 0.5)",
        })}
      >
        {props.value <= 25 ? (
          <IconImage
            className={styles.circularImageBlink}
            style={{ width: "1vw", marginTop: -5, color: iconColor }}
          />
        ) : (
          <IconImage
            style={{ width: "1vw", marginTop: -5, color: iconColor }}
          />
        )}
        <div style={{ fontSize: 10, marginTop: -3, color: "#fff" }}>
          <strong>{percentage}%</strong>
        </div>
      </CircularProgressbarWithChildren>
    </div>
  );
};

const AddItemToBar = (props: any) => {
  let IconName;
  switch (props.type) {
    case "money":
      IconName = LocalAtmOutlined;
      break;
    case "bank":
      IconName = AccountBalanceOutlined;
      break;
    case "time":
      IconName = ScheduleOutlined;
      break;
    case "date":
      IconName = DateRangeOutlined;
      break;
    default:
      IconName = LocalAtmOutlined;
  }
  return (
    <Grid container alignItems="center">
      <Grid item>
        <IconName />
      </Grid>
      <Grid item>
        <Typography style={{ fontWeight: "bold", fontSize: "1.2rem" }}>
          {props.body}
        </Typography>
      </Grid>
    </Grid>
  );
};
export default Hud;
