import {
  Button,
  Dialog,
  Grid,
  IconButton,
  Typography,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  TextField,
  Divider,
} from "@material-ui/core";
import React from "react";
import { useFormik } from "formik";
import * as Yup from "yup";
import styles from "./CharacterList.module.css";

import { loadUser } from "../../store/loadAllCharacter";

import Card from "../Global/Card/Card.js";
import CardAvatar from "../Global/Card/CardAvatar.js";
import CardBody from "../Global/Card/CardBody.js";

import avatar from "../../assets/asd.jpg";
import CardHeader from "../Global/Card/CardHeader.js";

import { DeleteForever } from "@material-ui/icons/";

import addCharacterLogo from "../../assets/addCharacter.png";
import store from "../../store";

export const CharacterInfo: React.FC<{
  id: number;
  name: string;
  money: number;
}> = (props) => {
  const [deleteDialog, setOpen] = React.useState(false);
  const [dialogValue, setDialogValue] = React.useState("");
  const handleDialogChange = (event: any) => {
    if (dialogValue === "") alert(dialogValue);
    else alert("WRONG");
  };

  let formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  });
  return (
    <Card profile className={styles.cardBody}>
      <CardAvatar profile className={styles.addCharIcon}>
        <img src={avatar} alt="..." />
      </CardAvatar>
      <CardBody profile>
        <Typography variant="subtitle1" align="left">
          : {props.name} (#{props.id})
          <br />
          : {formatter.format(props.money)}
        </Typography>
      </CardBody>
      <Grid item style={{ textAlign: "right" }}>
        <Button
          color="primary"
          variant="contained"
          onClick={() => {
            alert(props.id);
          }}
        >
          
        </Button>
        <IconButton
          className={styles.deleteButton}
          onClick={() => setOpen(true)}
        >
          <DeleteForever />
        </IconButton>
        <Dialog
          disableEscapeKeyDown
          open={deleteDialog}
          onClose={(event, reason) => {
            if (reason !== "backdropClick") {
              setOpen(false);
            }
          }}
        >
          <DialogTitle></DialogTitle>
          <DialogContent>
            <DialogContentText>
              <Typography variant="body1">
                 ,    ?
                <br />
              </Typography>
            </DialogContentText>
            <TextField
              autoFocus
              fullWidth
              helperText='    "",   .'
              onBlur={(event) => setDialogValue(event.target.value)}
            />
          </DialogContent>
          <DialogActions>
            <Button
              variant="contained"
              color="secondary"
              onClick={handleDialogChange}
            >
              
            </Button>
            <Button
              variant="outlined"
              color="inherit"
              onClick={() => setOpen(false)}
            >
              
            </Button>
          </DialogActions>
        </Dialog>
      </Grid>
    </Card>
  );
};

export const CharacterList: React.FC = () => {
  const [createDialog, setOpen] = React.useState(false);
  type CharacterInfo = {
    name: string;
    surname: string;
    age: number | null;
  };
  const initialValues: CharacterInfo = {
    name: "",
    surname: "",
    age: null,
  };

  const createCharacter = useFormik({
    initialValues: initialValues,
    validationSchema: Yup.object({
      name: Yup.string()
        .required("    .")
        .min(3, " -  - 3.")
        .max(24, " -  - 24.")
        .matches(
          /^[a-zA-Z]+$/,
          "       ."
        ),
      surname: Yup.string()
        .required("    .")
        .min(2, " -  - 2.")
        .max(24, " -  - 24.")
        .matches(
          /^[a-zA-Z]+$/,
          "       ."
        ),
      age: Yup.number()
        .typeError(" .")
        .required("    .")
        .min(16, "  - 16.")
        .max(80, "  - 80."),
    }),
    onSubmit: (values) => {
      alert(JSON.stringify(values));
    },
  });
  let characters = {
    char1: {
      id: store.getState().userSlice.char1.id,
      name: store.getState().userSlice.char1.name,
      money: store.getState().userSlice.char1.money,
    },
    char2: {
      id: store.getState().userSlice.char2.id,
      name: store.getState().userSlice.char2.name,
      money: store.getState().userSlice.char2.money,
    },
    char3: {
      id: store.getState().userSlice.char3.id,
      name: store.getState().userSlice.char3.name,
      money: store.getState().userSlice.char3.money,
    },
  };
  return (
    <div>
      <Card className={styles.mainCard}>
        <CardHeader color="primary">
          <Typography variant="h6"> </Typography>
        </CardHeader>
        <Grid
          container
          justifyContent="center"
          alignItems="center"
          style={{ minHeight: "40vh" }}
        >
          {Object.entries(characters).map(([key, character], i) =>
            character.id > 0 ? (
              <Grid key={i} item xs className={styles.gridItem}>
                <CharacterInfo
                  id={character.id}
                  name={character.name}
                  money={character.money}
                />
              </Grid>
            ) : (
              <Grid key={i} item xs className={styles.gridItem}>
                <Card profile className={styles.cardBody}>
                  <CardAvatar profile className={styles.addCharIcon}>
                    <img src={addCharacterLogo} alt="..." />
                  </CardAvatar>
                  <CardBody profile>
                    <Typography variant="h6" align="center">
                       
                    </Typography>
                  </CardBody>
                  <Grid item style={{ textAlign: "center" }}>
                    <Button
                      color="primary"
                      variant="contained"
                      style={{ marginBottom: "6px" }}
                      onClick={() => setOpen(true)}
                    >
                       
                    </Button>

                    <Dialog
                      disableEscapeKeyDown
                      open={createDialog}
                      onClose={(event, reason) => {
                        if (reason !== "backdropClick") {
                          setOpen(false);
                        }
                      }}
                      style={{ minWidth: "50vh" }}
                    >
                      <DialogTitle> </DialogTitle>
                      <form onSubmit={createCharacter.handleSubmit}>
                        <DialogContent>
                          <TextField
                            name="name"
                            className={styles.textfield}
                            label=""
                            fullWidth
                            value={createCharacter.values.name}
                            onChange={createCharacter.handleChange}
                            error={
                              createCharacter.touched.name &&
                              Boolean(createCharacter.errors.name)
                            }
                            helperText={
                              createCharacter.touched.name &&
                              createCharacter.errors.name
                            }
                          />
                          <TextField
                            name="surname"
                            className={styles.textfield}
                            label=""
                            fullWidth
                            value={createCharacter.values.surname}
                            onChange={createCharacter.handleChange}
                            error={
                              createCharacter.touched.surname &&
                              Boolean(createCharacter.errors.surname)
                            }
                            helperText={
                              createCharacter.touched.surname &&
                              createCharacter.errors.surname
                            }
                          />
                          <TextField
                            name="age"
                            label=""
                            fullWidth
                            value={createCharacter.values.age}
                            onChange={createCharacter.handleChange}
                            error={
                              createCharacter.touched.age &&
                              Boolean(createCharacter.errors.age)
                            }
                            helperText={
                              createCharacter.touched.age &&
                              createCharacter.errors.age
                            }
                          />
                        </DialogContent>
                        <DialogActions>
                          <Button
                            variant="contained"
                            type="submit"
                            disabled={!createCharacter.dirty}
                          >
                            
                          </Button>
                          <Button
                            variant="outlined"
                            color="inherit"
                            onClick={() => {
                              setOpen(false);
                              createCharacter.setSubmitting(false);
                            }}
                          >
                            
                          </Button>
                        </DialogActions>
                      </form>
                    </Dialog>
                  </Grid>
                </Card>
              </Grid>
            )
          )}
          <Divider orientation="vertical" flexItem />
          <Grid item xs className={styles.rightColumn}>
            <RightColumn />
          </Grid>
        </Grid>
      </Card>
    </div>
  );
};

const RightColumn = () => {
  return (
    <Typography>SocialClub: {store.getState().userSlice.login}</Typography>
  );
};

export default CharacterList;
