import { Grid, Typography } from "@material-ui/core";
import React from "react";
import styles from "./SpawnMenu.module.css";

import lastExitImage from "./../../assets/lastExit.png";
import defaultSpawnImage from "./../../assets/defaultSpawn.png";
import homeImage from "./../../assets/home.jpg";
import workImage from "./../../assets/work.png";

import LockIcon from "@material-ui/icons/Lock";

import Card from "./../../components/Global/Card/Card";
import CardBody from "../Global/Card/CardBody";
import CardHeader from "../Global/Card/CardHeader";
const SpawnMenu: React.FC = () => {
  return (
    <div className={styles.div}>
      <Card className={styles.card}>
        <CardHeader color="primary">
          <Typography variant="h5" color="initial">
              
          </Typography>
        </CardHeader>
        <CardBody>
          <Grid container spacing={1}>
            <Grid item xs>
              <SpawnOn image={lastExitImage} type={1} />
            </Grid>
            <Grid item xs>
              <SpawnOn image={defaultSpawnImage} type={2} />
            </Grid>
            <Grid item xs>
              <SpawnOff image={homeImage} type={3} />
            </Grid>
            <Grid item xs>
              <SpawnOff image={workImage} type={4} />
            </Grid>
          </Grid>
        </CardBody>
      </Card>
    </div>
  );
};

const SpawnOn = (props: any) => {
  let titleText: string;
  switch (props.type) {
    case 1:
      titleText = " ";
      break;
    case 2:
      titleText = " ";
      break;
    case 3:
      titleText = "";
      break;
    case 4:
      titleText = "";
      break;
    default:
      titleText = "Error";
  }
  const clicked = () => {
    alert(props.type);
  };
  return (
    <fieldset
      className={styles.fieldsetOn}
      style={{ backgroundImage: "url(" + props.image + ")" }}
      onClick={clicked}
    >
      <legend className={styles.legend}>{titleText}</legend>
      <Typography variant="h6" className={styles.text}>
        
      </Typography>
    </fieldset>
  );
};

const SpawnOff = (props: any) => {
  let titleText: string;
  switch (props.type) {
    case 1:
      titleText = " ";
      break;
    case 2:
      titleText = " ";
      break;
    case 3:
      titleText = "";
      break;
    case 4:
      titleText = "";
      break;
    default:
      titleText = "Error";
  }
  return (
    <fieldset
      className={styles.fieldsetOff}
      style={{ backgroundImage: "url(" + props.image + ")" }}
    >
      <legend className={styles.legend}>{titleText}</legend>
      <LockIcon className={styles.lockedIcon} />
      <Typography variant="subtitle1"></Typography>
    </fieldset>
  );
};

export default SpawnMenu;
